//main colors
$black: rgba(31, 32, 65, 1); //#1F2041
$purple: rgba(188, 156, 255, 1); //#BC9CFF
$green: rgba(111, 207, 151, 1); //#6FCF97
$white: rgba(255, 255, 255, 1); //#FFFFFF
$grey: rgba(229, 229, 229, 1); //#E5E5E5
$lightgrey: rgba(244, 244, 246, 1); //#F4F4F6;

//transparencies
$aFirst: 0.75;
$aSecond: 0.5;
$aThird: 0.25;
$aFourth: 0.05;

//additional transparencies
$aFifth: 0.2;
$aSixth: 0.1;
$aSeventh: 0.3;

//ui colors
$dark-shade-100: $black;
$dark-shade-75: rgba($black, $aFirst);
$dark-shade-50: rgba($black, $aSecond);
$dark-shade-25: rgba($black, $aThird);
$dark-shade-5: rgba($black, $aFourth);

//secondary colors
$purpleSecondary: rgba(139, 164, 249, 1); //#8BA4F9
$greenSecondary: rgba(102, 210, 234, 1); //#66D2EA
$blackSecondary: rgba(0, 0, 0, 1); //#000000



//gradients
$purpleGradient: linear-gradient(180deg, $purple 0%, $purpleSecondary 100%);
$purpleGradientTransparent: linear-gradient(180deg, rgba($purple, $aSecond) 0%, rgba($purpleSecondary, $aSecond) 100%);
$greenGradient: linear-gradient(180deg, $green 0%, $greenSecondary 100%);
$greenGradientTransparent: linear-gradient(180deg, rgba($green, $aFirst) 0%, rgba($greenSecondary, $aFirst) 100%);
$whiteGradient: linear-gradient(180deg, $white 0%, $white 100%);
$blackGradient: linear-gradient(90deg, rgba($blackSecondary, $aSecond) 0%, rgba($blackSecondary, 0) 99.99%, rgba($white, 0) 100%);

//font
$mainFont: Montserrat,
OpenSans,
sans-serif;
$mainFontSize: 16px;